import React, { useState, useEffect } from 'react';
import tarifas from '../data/tarifas.json';

const NotaryCalculator = () => {
  const [selectedTramite, setSelectedTramite] = useState('');
  const [cantidad, setCantidad] = useState(1);
  const [cuantia, setCuantia] = useState('');
  const [total, setTotal] = useState(0);
  const [desglose, setDesglose] = useState(null);

  const calcularTarifa = (tramite, cantidad = 1, cuantia = 0) => {
    if (!tramite) return { subtotal: 0, iva: 0, total: 0 };

    const tramiteInfo = tarifas.tramites.find(t => t.id === tramite);
    if (!tramiteInfo) return { subtotal: 0, iva: 0, total: 0 };

    if (tramiteInfo.tipo === 'cuantia') {
      let rango;
      if (cuantia <= 375) {
        // Para el primer rango, calculamos el 10% del canon
        const valor = cuantia * 0.10;
        const iva = valor * 0.15;
        return {
          subtotal: valor,
          iva: iva,
          total: valor + iva,
          descripcion: '10% del canon mensual'
        };
      } else {
        // Para los demás rangos, buscamos el rango correspondiente
        rango = tramiteInfo.rangos.find(r => 
          (cuantia >= r.min && (r.max === null || cuantia <= r.max))
        );
        if (rango) {
          return {
            subtotal: rango.valor,
            iva: rango.iva,
            total: rango.total,
            descripcion: `Rango de $${rango.min} a ${rango.max ? `$${rango.max}` : 'más'}`
          };
        }
      }
      return { subtotal: 0, iva: 0, total: 0 };
    }

    // Para trámites con tarifa fija
    const subtotal = tramiteInfo.tarifa_base * cantidad;
    const copias = tramiteInfo.copias * cantidad;
    const subtotalConCopias = subtotal + copias;
    const iva = subtotalConCopias * tramiteInfo.iva;
    
    return {
      subtotal: subtotalConCopias,
      iva: iva,
      total: subtotalConCopias + iva
    };
  };

  useEffect(() => {
    if (selectedTramite) {
      const tramiteInfo = tarifas.tramites.find(t => t.id === selectedTramite);
      if (tramiteInfo.tipo === 'cuantia') {
        if (cuantia) {
          const resultado = calcularTarifa(selectedTramite, 1, parseFloat(cuantia));
          setTotal(resultado.total);
          setDesglose(resultado);
        }
      } else {
        const resultado = calcularTarifa(selectedTramite, cantidad);
        setTotal(resultado.total);
        setDesglose(resultado);
      }
    }
  }, [selectedTramite, cantidad, cuantia]);

  const formatCurrency = (value) => {
    return new Intl.NumberFormat('es-EC', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 2
    }).format(value);
  };

  return (
    <div className="max-w-2xl mx-auto p-6 bg-white rounded-lg shadow-lg">
      <h2 className="text-2xl font-bold text-center mb-8">Calculadora de Costos Notariales</h2>
      
      <div className="space-y-6">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Tipo de Trámite
          </label>
          <select
            value={selectedTramite}
            onChange={(e) => {
              setSelectedTramite(e.target.value);
              setCuantia('');
              setCantidad(1);
            }}
            className="select-field"
          >
            <option value="">Seleccione un trámite</option>
            {tarifas.tramites.map((tramite) => (
              <option key={tramite.id} value={tramite.id}>
                {tramite.nombre}
              </option>
            ))}
          </select>
        </div>

        {selectedTramite && (
          <div>
            {tarifas.tramites.find(t => t.id === selectedTramite)?.tipo === 'cuantia' ? (
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Canon de Arrendamiento Mensual
                </label>
                <input
                  type="number"
                  value={cuantia}
                  onChange={(e) => setCuantia(e.target.value)}
                  placeholder="Ingrese el valor del canon mensual"
                  className="input-field"
                  min="1"
                  step="0.01"
                />
              </div>
            ) : (
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Cantidad
                </label>
                <input
                  type="number"
                  value={cantidad}
                  onChange={(e) => setCantidad(parseInt(e.target.value) || 1)}
                  className="input-field"
                  min="1"
                />
              </div>
            )}
          </div>
        )}

        {desglose && (
          <div className="mt-8 p-4 bg-gray-50 rounded-lg">
            <h3 className="text-lg font-semibold mb-4">Desglose de Costos</h3>
            <div className="space-y-2">
              {desglose.descripcion && (
                <p className="text-sm text-gray-600">{desglose.descripcion}</p>
              )}
              <div className="flex justify-between">
                <span>Subtotal:</span>
                <span className="font-medium calculator-numbers">{formatCurrency(desglose.subtotal)}</span>
              </div>
              <div className="flex justify-between">
                <span>IVA (15%):</span>
                <span className="font-medium calculator-numbers">{formatCurrency(desglose.iva)}</span>
              </div>
              <div className="flex justify-between text-lg font-bold mt-4 pt-4 border-t">
                <span>Total:</span>
                <span className="calculator-numbers">{formatCurrency(desglose.total)}</span>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default NotaryCalculator;
